.datavideos-edit-page .leftlayout {
  width: 430px;
  height: 100%;
  background-color: red;
}
.datavideos-edit-page .rightlayout {
  width: 30px;
  height: 30px;
  background-color: blue;
}
.datavideos-edit-page {
  height: 100%;
  overflow-y: hidden;
}
.datavideos-edit-page .ant-divider-horizontal {
  margin: 0px !important;
}
.datavideos-edit-page .content {
  height: calc(100% - 50px);
}
.datavideos-edit-page .content .focus-icon {
  background-color: #F0F0F2;
  padding-top: 16px;
}
.datavideos-edit-page .content .focus-icon .icon-btn {
  width: 50px;
  height: 55px;
}
.datavideos-edit-page .content .focus-icon .icon-btn .icon-pic {
  text-align: center;
  margin: 0 auto;
}
.datavideos-edit-page .content .focus-icon .icon-btn .icon-text {
  font-family: NotoSansHans-Regular;
  font-size: 11px;
  color: #000000;
  text-align: center;
  line-height: 12px;
}
.datavideos-edit-page .content .content-edit {
  width: calc(100%);
  height: 100%;
}
.datavideos-edit-page .content .content-edit .edit-video {
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  height: calc(100% - 185px);
  background-color: #F0F0F2;
  display: flex;
}
.datavideos-edit-page .content .content-edit .edit-video .left-content {
  height: 100%;
  display: flex;
  align-items: center;
  z-index: 2;
}
.datavideos-edit-page .content .content-edit .edit-video .left-content .left-sider {
  height: 100%;
  background-color: white;
  box-shadow: 3px 0 5px 0 rgba(210, 210, 210, 0.5);
}
.datavideos-edit-page .content .content-edit .edit-video .left-content .left-sider .ant-layout-sider-children {
  overflow: auto;
}
.datavideos-edit-page .content .content-edit .edit-video .left-content .left-control {
  position: absolute;
  width: 18px;
  height: 55px;
  background-color: white;
  box-shadow: 3px 0 5px 0 rgba(210, 210, 210, 0.5);
  border-radius: 0px 8px 8px 0px;
}
.datavideos-edit-page .content .content-edit .edit-video .left-content .left-control .control-btn {
  margin: 20px 0 20px 0;
}
.datavideos-edit-page .content .content-edit .edit-video .right-content {
  display: flex;
  height: 100%;
  align-items: center;
}
.datavideos-edit-page .content .content-edit .edit-video .right-content .right-sider {
  height: 100%;
  background-color: white;
  box-shadow: -3px 0 5px 0 rgba(210, 210, 210, 0.5);
  position: relative;
}
.datavideos-edit-page .content .content-edit .edit-video .right-content .right-sider .data-show {
  height: 80%;
}
.datavideos-edit-page .content .content-edit .edit-video .right-content .right-sider .datashow {
  z-index: 2;
  width: 100%;
  padding: 0px 0px 0px 0px;
  background: #FFFFFF;
  height: 100%;
}
.datavideos-edit-page .content .content-edit .edit-video .right-content .right-control {
  position: absolute;
  width: 18px;
  height: 55px;
  background-color: white;
  box-shadow: -3px 0 5px 0 rgba(210, 210, 210, 0.5);
  border-radius: 8px 0px 0px 8px;
  z-index: 1;
}
.datavideos-edit-page .content .content-edit .edit-video .right-content .right-control .control-btn {
  margin: 20px auto 20px auto;
  display: flex;
  justify-content: flex-end;
}
.datavideos-edit-page .content .content-edit .edit-fact {
  position: -webkit-sticky;
  position: sticky;
  z-index: 999;
  height: 215px;
  width: 100%;
  padding: 0;
  border-top: 2px solid #F0F0F2;
}
.ant-spin-nested-loading {
  position: none;
  width: 100%;
  height: 100%;
}
.ant-spin-nested-loading .ant-spin {
  max-height: 100% !important;
  margin-top: -50px;
}
.ant-spin-container {
  position: none;
  width: 100%;
  height: 100%;
  text-align: center;
}
